.services {
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  font-family: "Gentium Plus", serif;
  margin-top: 4%;
  margin-bottom: 10%;
}
.services .services-header-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.services .services-header-container .title1 {
  width: 100%;
  text-align: center;
  margin-top: 4%;
  color: #0C0C22;
  font-size: 2.1em;
  letter-spacing: -1.5px;
  padding-bottom: 0%;
  opacity: 1;
}
.services .services-header-container .line1 {
  width: 20%;
  opacity: 1;
  background-color: #0C0C22;
}
.services .cont-services {
  display: flex;
}
.services .cont-services .sub-cont-services1 {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
}
.services .cont-services .sub-cont-services1 img {
  width: 90%;
  border-radius: 2px;
  margin-top: 5%;
}
.services .cont-services .sub-cont-services1 div {
  border-bottom: 1px solid black;
  margin-top: 5%;
  width: 70%;
  display: flex;
  justify-content: space-around;
  align-items: baseline;
}
.services .cont-services .sub-cont-services1 div h3 {
  color: #0C0C22;
  font-size: 20px;
  text-align: left;
}
.services .cont-services .sub-cont-services1 div p {
  font-size: 10px;
  color: rgba(12, 12, 34, 0.4039215686);
  font-size: 0.95em;
  text-align: left;
}

@media (max-width: 900px) {
  .cont-services {
    flex-wrap: wrap;
  }
}/*# sourceMappingURL=Services.css.map */