.nav-container {
  width: 80%;
  background-color: #B9B4A8;
  border-radius: 20px;
  padding: 1% 3%;
  margin: 2% auto;
}

.items-container {
  width: 100%;
  justify-content: flex-end;
  gap: 2%;
  display: flex;
  flex-wrap: nowrap;
}
.items-container * {
  text-decoration: none;
  color: #5A5A58;
  transition: 1s;
  border-radius: 4px;
  padding: 1% 2%;
  font-family: "Gentium Plus", serif;
  font-size: 20px;
}
.items-container *:hover {
  color: black;
  transition: 1s;
  background-color: #c0c0c0;
}

.link {
  color: #444444;
}

@media (max-width: 768px) {
  .items-container {
    display: flex;
    justify-content: center;
    padding: 4% 0%;
  }
  .main-container .phrase-container {
    display: flex;
    font-size: 18px;
  }
  .nav-container {
    margin: 2% auto;
    width: 90%;
  }
}/*# sourceMappingURL=Navbar.css.map */